    },
    timestampToTime(timestamp) {
       var date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
       let  Y = date.getFullYear() + '-';
       let  M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
       let  D = (date.getDate()<10? '0'+ date.getDate():date.getDate());
        return Y+M+D;
     },
    get(day1, day2) {
      var getDate = function(str) {
        var tempDate = new Date();
        var list = str.split("-");
        tempDate.setFullYear(list[0]);
        tempDate.setMonth(list[1] - 1);
        tempDate.setDate(list[2]);
        return tempDate;
      }
      var date1 = getDate(day1);
      var date2 = getDate(day2);
      if(( date1.getFullYear() == date2.getFullYear() && date1.getMonth() == date2.getMonth() && date1.getDate() == date2.getDate() )){
        var dateArr = [];
        dateArr.push({day:day2,list:[]})
        return dateArr
      }else{
        date1.setDate(date1.getDate() + 1);
        var dateArr = [];
        var i = 0;
        while (!(date1.getFullYear() == date2.getFullYear()
          && date1.getMonth() == date2.getMonth() && date1.getDate() == date2
            .getDate())) {
          var dayStr =date1.getDate().toString();
          var newDate = (date1.getMonth() + 1).toString();
          if(dayStr.length ==1){
            dayStr="0"+dayStr;
          }
          if(newDate.length == 1){
            newDate = '0'+ newDate
          }
          dateArr[i] =({day:date1.getFullYear() + "-" + newDate + "-"
              + dayStr,list:[]}) ;
          i++;
          date1.setDate(date1.getDate() + 1);
        }
        dateArr.splice(0,0,{day:day1,list:[]})
        dateArr.push({day:day2,list:[]});
        return dateArr;
      }
    },
    prevYear(val){
      this.year -= 1
      this.isShow = false
      this.dateIndex = -1
      this.timeNumber = []
      if(val != 1){
        this.getActive()
      }
       
    },
    nextYear(val){
      this.year += 1
      this.isShow = false
      this.dateIndex = -1
      this.timeNumber = []
      if(val != 1){
        this.getActive()
      }
    },
    prevMonth(val){
      this.month -= 1
      if(this.month == 0){
        this.month = 12
        this.year -=1
      }

      this.isShow = false
      this.dateIndex = -1
      this.timeNumber = []
      if(val != 1){
        this.getActive()
      }
    },
    nextMonth(val){
      this.month += 1
      if(this.month == 13){
         this.month = 1
        this.year += 1
      }
      this.isShow = false
      this.dateIndex = -1
      this.timeNumber = []
      if(val != 1){
        this.getActive()
      }
    },
    getMonth(year,month){
      var day = new Date(year,month,0)
      var dayCount = day.getDate()
      return dayCount
    },
    getFirstDay(year,month){
      var day = new Date(year,month-1)
      var dayCount = day.getDay()
      if(dayCount == 0){
        dayCount = 7
      }
      return dayCount
    },
    changeColor(item,index){
      if(this.dateIndex == index){
        this.isShow = false
        this.dateIndex = -1
        this.timeNumber = []
        this.reMakeDate = ''
        // this.longTime = []
      }else{
        this.dateIndex = index
        this.dayIndex = index
        let d = item.toString()
        let m = this.month.toString()
        if(item < 10){
          d = '0' + d
        }
        if(m < 10){
          m = '0' + m
        }
        let day = this.year + '-' + m + '-'+ d
        this.reMakeDate = day
        // this.longTime = [day,day]
        this.getSlotDate(day)
      }
    },
    saveActive(){
      if(!this.isPrice){
        if(this.isRemake){
           if(!this.reMakeDate){
              this.$message({type: 'error',message: '请填写活动时间段'})
            }else if(!this.personNum || !this.reg.test(this.personNum)){
              this.$message({type: 'error',message: '请填写正确活动人数（正整数）'})
            }else if( this.Oreg.test(this.zkCombo.oldPrice)||!this.zkCombo.oldPrice||!this.priceReg.test(this.zkCombo.oldPrice)){
                this.$message({type: 'error',message: '请填写完整标准折扣价（金额大于1最多保存两位小数）'})
              }else if(!this.zkCombo.discount || this.Oreg.test(this.zkCombo.discount)){
                this.$message({type: 'error',message: '请填写标准价折扣（折扣大于1最多保存两位小数）'})
              }else if(this.Oreg.test(this.zkCombo.oldChildPrice)&&this.zkCombo.oldChildPrice||this.zkCombo.oldChildPrice && !this.priceReg.test(this.zkCombo.oldChildPrice)){
              this.$message({type: 'error',message: '请填写完整儿童折扣价（金额大于1最多保存两位小数）'})
             }else if(this.Oreg.test(this.zkCombo.childDiscount)&&this.zkCombo.oldChildPrice || this.zkCombo.oldChildPrice && !this.zkCombo.childDiscount){
                this.$message({type: 'error',message: '请填写儿童价价折（折扣大于1最多保存两位小数）'})
             }else{
              this.checkCombo(1) 
            }
          }else{
              if(!this.allTimeL.length ){
                this.$message({type: 'error',message: '请填写活动时间段'})
              }else if(!this.activeNum && !this.isRemake||!this.reg.test(this.activeNum) && !this.isRemake){
                this.$message({ type: 'error', message: '请填写正确活动数量（正整数）'})
              }else if(!this.personNum || !this.reg.test(this.personNum)){
                this.$message({type: 'error',message: '请填写正确活动人数（正整数）'})
              }else if( this.Oreg.test(this.zkCombo.oldPrice)||!this.zkCombo.oldPrice||!this.priceReg.test(this.zkCombo.oldPrice)){
                this.$message({type: 'error',message: '请填写完整标准折扣价（金额大于1最多保存两位小数）'})
              }else if(!this.zkCombo.discount || this.Oreg.test(this.zkCombo.discount)){
                this.$message({type: 'error',message: '请填写标准价折扣（折扣大于1最多保存两位小数）'})
              }else if(this.Oreg.test(this.zkCombo.oldChildPrice)&&this.zkCombo.oldChildPrice||this.zkCombo.oldChildPrice && !this.priceReg.test(this.zkCombo.oldChildPrice)){
              this.$message({type: 'error',message: '请填写完整儿童折扣价（金额大于1最多保存两位小数）'})
             }else if(this.Oreg.test(this.zkCombo.childDiscount)&&this.zkCombo.oldChildPrice || this.zkCombo.oldChildPrice && !this.zkCombo.childDiscount){
                this.$message({type: 'error',message: '请填写儿童价价折（折扣大于1最多保存两位小数）'})
             }else{
                this.checkCombo(1) 
              }
            }
        }else{
          if(this.isRemake){
             if(!this.reMakeDate){
              this.$message({type: 'error',message: '请填写活动时间段'})
            }else if(!this.personNum || !this.reg.test(this.personNum)){
              this.$message({type: 'error',message: '请填写正确活动人数（正整数）'})
            }else if(this.Oreg.test(this.bzPrice)||!this.bzPrice || !this.priceReg.test(this.bzPrice)){
              this.$message({type: 'error',message: '请填写正确金额（大于1最多保存两位小数)'})
            }else if(this.etPrice &&this.Oreg.test(this.etPrice)||this.etPrice && !this.priceReg.test(this.etPrice)){
              this.$message({type: 'error',message: '请填写正确儿童金额（大于1最多保存两位小数）'})
            }else{
              this.checkCombo(2)
            }
          }else{
            if(!this.allTimeL.length){
              this.$message({type: 'error',message: '请填写活动时间段'})
            }else if(!this.activeNum && !this.isRemake||!this.reg.test(this.activeNum) && !this.isRemake){
              this.$message({ type: 'error', message: '请填写正确活动数量（正整数）'})
            }else if(!this.personNum || !this.reg.test(this.personNum)){
              this.$message({type: 'error',message: '请填写正确活动人数（正整数）'})
            }else if(this.Oreg.test(this.bzPrice)||!this.bzPrice || !this.priceReg.test(this.bzPrice)){
              this.$message({type: 'error',message: '请填写正确标准金额（大于1最多保存两位小数）'})
            }else if(this.etPrice &&this.Oreg.test(this.etPrice)||this.etPrice && !this.priceReg.test(this.etPrice)){
              this.$message({type: 'error',message: '请填写正确儿童金额（大于1最多保存两位小数）'})
            }else{
              this.checkCombo(2)
            }
          }
       }
    },
    delet(item,index,colorIndex){
      this.$confirm('此操作将删除当天该时间段, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.delatTime(item,index,colorIndex)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    delatTime(item,index,colorIndex){
      this.$http.post(this.api + '/ActivitySlotDelTwo',{
        token: localStorage.getItem('token'),
        activity_id: this.active_id ,
        version: 2.0,
        slot_id: item.slot_id,
        is_all:0
      })
        .then(res=>{
          if(res.data.code == 1){
            this.timeNumber.splice(index,1)
            if(colorIndex){
              this.allDay[colorIndex].list = this.timeNumber
            }
            if(!this.timeNumber.length){
              this.color[this.dayIndex].index = '1'
              this.allDay.splice(colorIndex,1)
            }
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
          
          }else if(res.data.code == 3){
            this.$http.post(this.api + '/home/index/token')
              .then(res=>{
                localStorage.setItem('token',res.data.data)
                this.delatTime(item,index,colorIndex)
              })
          }else if(res.data.code == 0){
            alert(res.data.msg)
          }
        })
    },
    getAllDay(){
      var day = this.getMonth(this.year,this.month)
      var dayIn = this.getFirstDay(this.year,this.month)
      var newDay = new Date().getTime()
      var temp =[]
      var color = []
      let b = []
      for(let i = 1;i<dayIn;i++){
        temp.push(' ')
        color.push({index:0})
      }
    
      for(let i = 1;i<=day;i++) {
        temp.push(i)
        b.push({index: 1})
        for (let j = 0; j < this.allDay.length; j++) {
          if(new Date(this.year, this.month - 1, i).getTime() >= Date.now() - 8.64e7){
            if (new Date(this.year, this.month - 1, i).getTime() == this.parseDate(this.allDay[j].date)  && this.allDay[j].status !=2 ) {
              b[i - 1] = ({index: 0})
             // for(let m =0; m<this.allDay[j].list.length;m++){
              //   if(this.allDay[j].list[m].online != 1){
              //     b[i - 1] = ({index: 0})
              //   }else{
              //     if(b[i-1].index == 0){
              //       b[i - 1] = ({index: 0})
              //     }else{
              //       b[i - 1] = ({index: 3})
              //     }
              //   }
              // }
            } else {
              if(b[i-1].index == 0){
                b[i - 1] = ({index: 0})
              }else{
                b[i - 1] = ({index: 1})
              }
            }
          }else{
            b[i - 1] = ({index: 3})
          }
        }
      }
      this.color = color.concat(b)
      this.dayCount = temp
    },
    parseDate(str) { // 字符串转时间戳
      return Date.parse(new Date(Date.parse(str.replace(/-/g, "/")))) ; // 精确到秒
    },
    getweekDay(){
      var day = this.getMonth(this.year,this.month)
      var dayIn = this.getFirstDay(this.year,this.month)
      var temp =[]
      var color = []
      let b = []
      for(let i = 1;i<dayIn;i++){
        temp.push(' ')
        color.push({index:0})
      }
      for(let i = 1;i<=day;i++){
        temp.push(i)
        b.push({index:1})
        for (let j = 0; j < this.allDay.length; j++) {
          if(new Date(this.year, this.month - 1, i).getTime() >= Date.now() - 8.64e7){
            if (new Date(this.year, this.month - 1, i).getTime() == this.parseDate(this.allDay[j].date) && this.allDay[j].status !=2 ) {
              b[i - 1] = ({index: 0})
              // for(let m =0; m<this.allDay[j].list.length;m++){
              //   if(this.allDay[j].list[m].online != 1){
              //      b[i - 1] = ({index: 0})
              //   }else{
              //     if(b[i-1].index == 0){
              //       b[i - 1] = ({index: 0})
              //     }else{
              //       b[i - 1] = ({index: 3})
              //     }
              //   }
              // }
            } else {
              if(b[i-1].index == 0){
                b[i - 1] = ({index: 0})
              }else{
                b[i - 1] = ({index: 1})
              }
            }
          }else{
            b[i - 1] = ({index: 3})
          }
        }
      }
      this.color = color.concat(b)
      this.dayCount = temp
      this.abiosh()
    },
    deletDate(){
      this.$confirm('此操作将删除已选取的时间, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deletAllDay()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    deletAllDay(){
     this.$http.post(this.api + '/ActivitySlotDelTwo',{
       token: localStorage.getItem('token'),
       activity_id: this.active_id,
       is_all:1,
       verson: 2.0,
     })
       .then(res=>{
         if(res.data.code == 1){
           this.getActive()
           this.$message({
             type: 'success',
             message: '删除成功!'
           });
         }else if(res.data.code == 3){
        
            this.deletAllDay()
          
         }else if(res.data.code == 0){
           alert(res.data.msg)
         }
       })
    },
    //修改活动
    remake(item,index){
       this.asdsad(item,index)
    },
    asdsad(item,index){
    
      this.discountChose = []
      this.isRemake = true
      this.witchDay = this.reMakeDate +' '+ item.begin_time + '-'+ item.end_time
      this.remakeIndex = index
      this.slot_id = item.slot_id        
      // this.longTime.push(item.begin_date,item.end_date)
      this.activeList = [{begin_time: item.begin_time,end_time:item.end_time}]
      this.personNum = item.max_person_num
      this.price = item.price
      if(item.kind == 1){
        this.zkCombo = item.price
        this.discountChose.push('1')
      }else{
        this.bzPrice = item.price[0]
        this.etPrice = item.price[1]
      }
      if(item.qzCombo.length){
         this.pcCombo = item.qzCombo
         this.discountChose.push('2')
      }
      if(item.zhCombo.length){
         this.zhCombo = item.zhCombo
        this.discountChose.push('3')
      }
    },

    getActives(){
      this.$http.post(this.api + '/ActivityE',{
        token: localStorage.getItem('token'),
        activity_id: this.active_id,
        visit: 0
      })
        .then(res=>{
          if(res.data.code == 1){
            let data = res.data.data
            if(data.long_day == 1){
              this.allDay = data.slot
              if(this.allDay.length){
                this.getweekDay()
              }
            }
          }else if(res.data.code == 3){
           this.getActives()
          }else if(res.data.code == 0){
            alert(res.data.msg)
          }
        })
    },
    
    //获取已创建套餐
    isCombine(val,combine){
      if(!combine){
          this.$http.post(this.api + '/CombineUsedTwo',{
          token: localStorage.getItem('token'),
          version: 2.0,
          activity_id: this.active_id,
          type: val
        })
        .then(res=>{
          if(res.data.code == 1){
            var data = res.data.data
            if(data.length){
              if(val == '1'){
                this.isCreate = true
              }else{
                this.isZhemo = true
              }
            }
          }else if(res.data.code == 3){
            this.getCombine(val)
          }else if(res.data.cdoe == 0){
            this.$message({type: 'error',message: res.data.msg})
          }
        })
      }
    
    },
    getCombine(val,combine){
      if(!combine){
          this.$http.post(this.api + '/CombineUsedTwo',{
          token: localStorage.getItem('token'),
          version: 2.0,
          activity_id: this.active_id,
          type: val
        })
        .then(res=>{
          if(res.data.code == 1){
            var data = res.data.data
              var arr = []
              if(data.length){
                if(val == '1'){
                  data.forEach((item) => {
                  arr.push(item)
                  })
                  this.pcCombo = arr
                  this.isQzCombine = true
                }else{
                  data.forEach((item) => {
                  arr.push(item)
                  })
                  this.zhCombo = arr
                  this.iszhCombo = true
                }
              }
          }else if(res.data.code == 3){
            this.getCombine(val)
          }else if(res.data.cdoe == 0){
            this.$message({type: 'error',message: res.data.msg})
          }
        })
      }
    },
    paduanCheck(){
  
        this.isCombine('1',this.isQzCombine)
    
        this.isCombine('2',this.iszhCombo)
  
    },
    getActive(){
      this.$http.post(this.api + '/ActivityAllSlotDetailTwo',{
        token: localStorage.getItem('token'),
        activity_id: this.active_id,
        visit: 0,
        version: 2.0
      })
        .then(res=>{
          if(res.data.code == 1){
            let data = res.data.data
              for(let i =0;i<data.length;i++){
                if(data[i].status == 0 && data[i].online != 1){
                    var arr = data[i]
                    var price = {
                        oldPrice:arr.price_origin,
                        discount:arr.price_discount,
                        disprice:arr.price,
                        oldChildPrice:arr.kids_price_origin,
                        childDiscount:arr.kids_price_discount,
                        childDisprice:arr.kids_price
                    }
                    var priceArr = [
                     arr.price_origin, arr.kids_price_origin
                    ]
                    var qzCombo = [],
                        zhCombo = [];
                    for(var j = 0; j< arr.combine.length;j++){
                      if(arr.combine[j].type == 1){
                        qzCombo.push(arr.combine[j])
                      }else{
                        zhCombo.push(arr.combine[j])
                      }
                    }
                    this.isTimeNumber.push(
                      {
                        slot_id: arr.slot_id,
                        begin_date: arr.begin_date,
                        end_date: arr.end_date,
                        begin_time: arr.begin_time,
                        end_time: arr.end_time,
                        max_person_num: arr.max_person_num,
                        price: arr.is_discount ==1? price: priceArr,
                        kind: arr.is_discount ==1? '1' : '2',
                        qzCombo:qzCombo,
                        zhCombo:zhCombo
                      }
                    )
                 }
              }
              this.allDay = data
              this.getweekDay()
          }else if(res.data.code == 3){
                this.getActive()
          }else if(res.data.code == 0){
            this.$alert(res.data.msg)
          }
        })
    },
    getPriceAcity(){
      this.$http.post(this.api + '/ActivityETwo',{
        token: localStorage.getItem('token'),
        activity_id: this.active_id,
        visit: 0
      })
      .then(res=>{
        if(res.data.code == 1){
          var data = res.data.data
           this.minOld =  data.kids_stand_low
            this.maxOld = data.kids_stand_high
            this.minJoinOld = data.age_limit
            if(data.differ.length){
               this.cjList = data.differ
               this.chaJia = true
            }
        }else if(res.data.cdoe == 3){
          this.getPriceAcity()
        }else if(res.data.code == 0){
          this.$message({type: 'warning',message: res.data.msg})
        }
      })
    },
  },
  
  mounted(){
      this.getActive()
    //  this.getActives()
     this.getPriceAcity()
  },
  created(){
    window.scroll(0,0)
  }
  }



